import {
	baseURL,
	
} from './base.js'; //
  export function classification() {  //
  	return myRequest({
  		url:'queryCategories',
  		// url:'queryGoodsByCategory?category=%E6%89%8B%E6%9C%BA',queryPromoteGoods
  	}
  	)
  }
  export function classcontent(code) {  //
  	return myRequest({
  		url:'queryGoodsByCategory?category='+code,
		data:code
  		// url:'queryGoodsByCategory?category=%E6%89%8B%E6%9C%BA',queryPromoteGoods
  	}
  	)
  }
 export function recommend() {  //
 	return myRequest({
		url:'queryPromoteGoods',
 		// url:'queryGoodsByCategory?category=%E6%89%8B%E6%9C%BA',queryPromoteGoods
 	}
	)
 }
 export function pic(code) {  //
 	return myRequest({
		
		 responseType: 'arraybuffer',
		 encoding: 'base64',
 		url:'queryGoodsLogo?code='+code,
		data:code
 	}
 	)
 }
 
export const myRequest = (options) => {
	return new Promise((resolve, reject) => {
		uni.request({
			url: baseURL + options.url, //+
			method: options.method || 'GET', //GET
			data: options.data || {}, //
			// headers: {
			//     'Authorization ': window.localStorage.getItem('token') //
			// },
			success: (res) => {
				resolve(res)
			},
			// 
			fail: (err) => {
				console.log(err)
				reject(err)
			}
		})
	})
}